import React from 'react'
import { Link } from 'react-router-dom';

import ViewList from './viewList'
import axios from '../../API.js'

class App extends React.Component {

  constructor(props)
  {    
    super(props);    
    this.state = {
      viewdata:[]
    }
  }
  addnew=()=>{
    this.props.history.push('/requester/add');
  }
  deleteItem=(id)=>{
    var r = window.confirm('Are u want to Delete?')
    if(r===true) {
      axios.post("/Requesters/Update",{id:id,action:'del',userid:this.context.userdata.userid}).then(res=>{
        if(res.status==='success')
          this.loadDate();
      })
    }    
  }
  loadDate=()=>{
    axios.get("/Requesters").then(res=>{
      if(res.data.status==='success')
        this.setState({viewdata:res.data.data})       
    
    })
  }
  componentDidMount=()=> {
    this.loadDate();
  }
  render() {
    const data={
      columns: [
      {            
        label: 'ID',
        field: 'id',
        sort: 'asc',
      },
      {            
        label: 'Requester',
        field: 'requester',
        sort: 'asc',
      },
      {
        label: 'Title',
        field: 'title',
        sort: 'asc',
      },
      {
        label: 'Reward',
        field: 'reward',
        sort: 'asc',
      },
      {
        label: 'Action',
        field: 'action',
      }], 
      rows: [...this.state.viewdata.map((res,i)=>({
        id:res.ID,
        requester:res.RequesterName,
        title:res.Title,
        reward:res.Reward,
        action:<><Link className="btn btn-primary btn-sm" to={{pathname:"/requester/add",state:{id:res.ID} }} onClick={this.toggle}>Edit</Link>&nbsp;<Link to={{}} className="btn btn-danger btn-sm" onClick={()=>this.deleteItem(res.ID)}>Delete</Link></>
      }))]
    }
    let view=(
    <>

    <div className="menubar"> 
    <ViewList header="Requesters" data={data} addnew={this.addnew}/>
    </div>

    </>
    )
    return view;
  }
}
export default App;